.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: #122920;
  color: rgb(204, 169, 71);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 5%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
  font-family: sans-serif;
  border-bottom: 3px solid white;
}

.main-image {
  width: 100%;
  height: 35rem;
  z-index: 0;
  overflow: hidden;
}

.main-image img {
  width: 110%;
  height: 100%;
  object-fit: cover;
  transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
}

@media only screen and (max-width: 502px) {
  .header h1 {
    font-size: 0.9rem;
  }
}
