.summary {
  text-align: center;
  max-width: 45rem;
  width: 90%;
  margin: auto;
  margin-top: -10rem;
  position: relative;
  background-color: #383838;
  color: rgb(216, 135, 13);
  border-radius: 14px;
  padding: 1rem;
  box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
  border: 4px solid rgb(214, 201, 20);
}

.summary h2 {
  font-size: 2rem;
  margin-top: 0;
}
